import { db } from './firebase';
import { collection, getDocs, getDoc, doc, setDoc, deleteDoc, query, where, writeBatch } from 'firebase/firestore';
import type { Vehicle } from './types';
import { unstable_noStore as noStore } from 'next/cache';

async function mapDocToVehicle(doc: any): Promise<Vehicle> {
    const data = doc.data();
    return {
        id: parseInt(doc.id),
        make: data.make,
        model: data.model,
        year: data.year,
        price: data.price,
        mileage: data.mileage,
        description: data.description,
        type: data.type,
        images: data.images,
        specifications: data.specifications
    };
}

export async function fetchVehicles(filters?: { type?: string, price?: string }): Promise<Vehicle[]> {
    noStore();
    try {
        const vehiclesCollection = collection(db, 'vehicles');
        let q = query(vehiclesCollection);

        if (filters?.type && filters.type !== 'all') {
            q = query(q, where('type', '==', filters.type));
        }

        let vehicles = (await getDocs(q)).docs.map(mapDocToVehicle);

        if (filters?.price && filters.price !== 'all') {
            const [min, max] = filters.price.split('-').map(Number);
            vehicles = vehicles.filter(v => {
                if (max) {
                    return v.price >= min && v.price <= max;
                }
                return v.price >= min;
            });
        }
        
        return Promise.all(vehicles);
    } catch (error) {
        console.error("Error fetching vehicles:", error);
        return [];
    }
}

export async function fetchVehicleById(id: string): Promise<Vehicle | null> {
    noStore();
    try {
        const vehicleDocRef = doc(db, 'vehicles', id);
        const vehicleDoc = await getDoc(vehicleDocRef);

        if (vehicleDoc.exists()) {
            return await mapDocToVehicle(vehicleDoc);
        } else {
            return null;
        }
    } catch (error) {
        console.error("Error fetching vehicle by ID:", error);
        return null;
    }
}

export async function fetchAllVehicleIds() {
    noStore();
    try {
        const vehiclesCollection = collection(db, 'vehicles');
        const vehicleSnapshot = await getDocs(vehiclesCollection);
        return vehicleSnapshot.docs.map(doc => ({ id: doc.id }));
    } catch (error) {
        console.error("Error fetching vehicle IDs:", error);
        return [];
    }
}

export async function fetchFeaturedVehicles(limit = 4) {
    noStore();
    try {
        const vehicles = await fetchVehicles();
        // For now, we'll just take the first few as "featured"
        return vehicles.slice(0, limit);
    } catch (error) {
        console.error("Error fetching featured vehicles:", error);
        return [];
    }
}
